.banner-section {
  padding-top: 70px;
  min-height: calc(100vh - 100px);
  grid-area: banner; }
  @media only screen and (max-width: 1024px) {
    .banner-section {
      min-height: calc(100vh - 800px);
      padding-top: 50px; } }
  .banner-section .banner-container {
    display: flex;
    align-items: center;
    justify-content: center; }
    @media only screen and (max-width: 1024px) {
      .banner-section .banner-container {
        flex-direction: column-reverse;
        text-align: center; } }
    .banner-section .banner-container .main-title {
      margin-bottom: 10px;
      padding-top: 10px; }
    .banner-section .banner-container .banner-image-wheel {
      margin: 25px 0;
      width: 100%;
      position: relative; }
      @media only screen and (max-width: 1024px) {
        .banner-section .banner-container .banner-image-wheel {
          margin: 5px; } }
      .banner-section .banner-container .banner-image-wheel .banner-image {
        border-radius: 50%;
        border: #BABABA 3px solid;
        overflow: hidden;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%; }
        .banner-section .banner-container .banner-image-wheel .banner-image img {
          margin-left: 50%;
          transform: translateX(-50%); }
